import React from "react";
import "./index.scss";
import {
  List,
  Radio,
  Flex,
  Button,
  TextareaItem,
  InputItem
} from "antd-mobile";
// import CusFrom from '../../components/form2';
const RadioItem = Radio.RadioItem;

export default class UserInfo extends React.Component {
  constructor() {
    super();
    this.state = {
      value: 0,
      educationVal: 4
    };
  }
  onChange = value => {
    console.log(value);
    this.setState({
      value: value
    });
  };
  choiceEdu = educationVal => {
    console.log(educationVal);
    this.setState({
      educationVal
    });
  };
  render() {
    const { value, educationVal } = this.state;
    const data = [
      { value: 0, label: "男" },
      { value: 1, label: "女" }
    ];
    const education = [
      { value: 0, label: "没读过书" },
      { value: 1, label: "小学" },
      { value: 2, label: "初中" },
      { value: 3, label: "高中" },
      { value: 4, label: "大学" }
    ];
    return (
      // <CusFrom />
      <div className="userinfo">
        <div className="title">疫情期间的心理评估</div>
        {/* <List >
        {data.map(i => (
          <RadioItem key={i.value} checked={value === i.value} onChange={() => this.onChange(i.value)}>
            {i.label}
          </RadioItem>
        ))}
      </List> */}
        <div className="choice-container">
          <div className="choice-title">你的性别()?</div>
          <div className="choice">
            <List>
              {data.map(i => (
                <RadioItem
                  key={i.value}
                  checked={value === i.value}
                  onChange={() => this.onChange(i.value)}
                >
                  {i.label}
                </RadioItem>
              ))}
            </List>
          </div>
        </div>
        <div className="choice-container">
          <div className="choice-title">你的年龄?</div>
          <div className="choice">
            <InputItem
              type="money"
              placeholder=""
              clear
              moneyKeyboardAlign="left"
              onChange={v => {
                console.log("onChange", v);
              }}
              onBlur={v => {
                console.log("onBlur", v);
              }}
            ></InputItem>
          </div>
        </div>
        <div className="choice-container">
          <div className="choice-title">文化程度？</div>
          <div className="choice">
            <List>
              {education.map(i => (
                <RadioItem
                  key={i.value}
                  checked={educationVal === i.value}
                  onChange={() => this.choiceEdu(i.value)}
                >
                  {i.label}
                </RadioItem>
              ))}
            </List>
            
          </div>
        </div>

        <div className="choice-container">
          <div className="choice-title">当前居住地?</div>
          <div className="choice">
            <TextareaItem
              title=""
              placeholder=""
              data-seed="logId"
              ref={el => (this.autoFocusInst = el)}
              autoHeight
            />
          </div>
        </div>

        <div className="choice-container">
          <div className="next-btn">
            <Button type="primary">下一步</Button>
          </div>
        </div>
      </div>
    );
  }
}
